import React from 'react';
import T from 'prop-types';
import Heading from '@splunk/react-ui/Heading';
import Markdown from '@splunk/react-ui/Markdown';
import styled from 'styled-components';

const Option = styled.div`
    margin-top: 20px;
`;
const OptionName = styled.div`
    color: #5ca300;
    padding-bottom: 5px;
`;
const PairContainer = styled.div`
    margin-left: 40px;
    margin-top: 10px;
`;
const PairTitle = styled.span`
    display: inline-block;
    color: #8293a7;
    width: 80px;
`;
const PairText = styled.span``;

const Pair = ({ title, text }) => (
    <PairContainer>
        <PairTitle>{`${title}:`}</PairTitle>
        <PairText>{text}</PairText>
    </PairContainer>
);

Pair.propTypes = {
    title: T.string,
    text: T.string,
};

const OptionItem = ({ name, type, desc, defaultValue }) => (
    <Option>
        <OptionName>{name}</OptionName>
        <Markdown style={{ marginLeft: 40 }} text={desc} />
        <Pair title="Type" text={type} />
        {defaultValue && <Pair title="Default" text={defaultValue} />}
    </Option>
);

OptionItem.propTypes = {
    name: T.string,
    type: T.string,
    desc: T.string,
    defaultValue: T.string,
};

const OptionsDoc = ({ options, description }) => (
    <div>
        {options.length === 0 ? (
            <Heading level={4}> No Options</Heading>
        ) : (
            <Heading level={4}>Options</Heading>
        )}
        {description && <div data-test="option-description">{description}</div>}
        {options.map(d => (
            <OptionItem key={d.name} {...d} />
        ))}
    </div>
);

OptionsDoc.propTypes = {
    options: T.array.isRequired,
    description: T.string,
};

export default OptionsDoc;
